@charset "UTF-8";
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #333;
  color: white;
}

select {
  text-align: center;
}

.container {
  margin-top: 5.5rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  overflow: hidden;
}

.container h1 {
  font-size: 3rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.container .input-city {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 3rem;
}

.container .input-city button {
  border: none;
  outline: none;
  -webkit-box-flex: .2;
      -ms-flex: .2;
          flex: .2;
  font-size: 1.5rem;
  font-weight: 800;
  background-color: #14213d;
  color: white;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  cursor: pointer;
  -webkit-transition: .06s;
  transition: .06s;
}

.container .input-city button:hover {
  background-color: #24217d;
}

.container .input-city button:active {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.container input {
  outline: none;
  border: none;
  padding: .2em 1em;
  font-size: 1.5rem;
  width: 35%;
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

.container h1 {
  margin-bottom: 15px;
}

.container .city {
  width: 35%;
  text-align: center;
  font-size: 1.7rem;
  font-weight: 400;
  padding: .7em .5em;
  border-radius: 7px;
  background-color: #555;
  position: absolute;
  top: 180px;
  margin-bottom: -.85rem;
  z-index: 9;
}

.container .city:hover {
  background-color: #666;
  z-index: 10;
}

@media (max-width: 600px) {
  .container input {
    width: 60%;
  }
  .container .city {
    width: 260px;
    margin-top: 6px;
  }
}

@media (min-width: 601px) {
  .container .city {
    width: 370px;
  }
}

@media (max-width: 600px) {
  :root {
    font-size: 12px;
  }
}

.card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  padding: 2.5em;
  margin-top: 100px;
  width: 22rem;
  height: 22rem;
  border-radius: 5px;
  background-color: white;
  color: #212121;
  margin-bottom: 14vh;
}

.card .weather-icon {
  background-color: #14213d;
  border-radius: 10px;
  width: 7rem;
  height: 7rem;
  background-position: center;
  background-size: cover;
}

.card .heading {
  text-align: end;
  margin-bottom: 2em;
}

.card .mid-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
      -ms-flex-flow: row-reverse;
          flex-flow: row-reverse;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
}

.card .mid-row h1 {
  font-size: 3rem;
  font-weight: 500;
  position: relative;
  text-align: center;
}

.card .mid-row h1 p {
  font-size: 1.2rem;
  font-weight: 900;
}

.card .mid-row h1::before {
  content: "o";
  position: absolute;
  top: -8px;
  right: -20px;
  font-size: 2rem;
  font-weight: 500;
}

.card .mid-row h1::after {
  content: "درجة الحرارة";
  position: absolute;
  top: -1.5rem;
  left: -1rem;
  font-size: 1.2rem;
  font-weight: 900;
  width: 7.5rem;
}

.error {
  margin-top: 100px;
  font-size: 2rem;
}
