$width: 30%;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #333;
  color: white;
}


select {
  text-align: center;
}

.container {
  margin-top: 5.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  // min-height: 100vh;
  overflow: hidden;

  h1 {
    font-size: 3rem;
    font-weight: 600;
    margin-bottom: 1rem;
  }

  .input-city {
    display: flex;
    height: 3rem;

    button {
      border: none;
      outline: none;
      flex: .2;
      font-size: 1.5rem;
      font-weight: 800;
      background-color: #14213d;
      color: white;
      border-bottom-right-radius: 10px;
      border-top-right-radius: 10px;
      cursor: pointer;
      transition: .06s;

      &:hover {
        background-color: #24217d;
      }

      &:active {
        transform: scale(1.1);
      }
    }
  }

  input {
    outline: none;
    border: none;
    // height: 2rem;
    padding: .2em 1em;
    font-size: 1.5rem;
    width: 35%;
    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
    flex: 1;
  }

  h1 {
    margin-bottom: 15px;
  }

  .city {
    width: 35%;
    text-align: center;
    font-size: 1.7rem;
    font-weight: 400;
    padding: .7em .5em;
    border-radius: 7px;
    background-color: #555;
    position: absolute;
    top: 180px;
    // left: 29%;
    margin-bottom: -.85rem;
    z-index: 9;

    &:hover {
      background-color: #666;
      z-index: 10;
    }

  }

  @media (max-width:600px) {
    input {
      width: 60%;
    }

    .city {
      width: 260px;
      margin-top: 6px;
    }
  }

  // @media (min-width:1000px) {
  //   .city {
  //     width: 370px;
  //   }
  // }

  @media (min-width:601px) {
    .city {
      width: 370px;
    }
  }

}

@media (max-width:600px) {
  :root {
    font-size: 12px;
  }
}

.card {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  flex-flow: column;
  padding: 2.5em;
  margin-top: 100px;
  width: 22rem;
  height: 22rem;
  border-radius: 5px;
  background-color: rgb(255, 255, 255);
  color: rgb(33, 33, 33);
  margin-bottom: 14vh;

  .weather-icon {
    background-color: #14213d;
    border-radius: 10px;
    width: 7rem;
    height: 7rem;
    background-position: center;
    background-size: cover;
  }

  .heading {
    text-align: end;
    margin-bottom: 2em;
  }

  .mid-row {
    display: flex;
    flex-flow: row-reverse;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    h1 {
      font-size: 3rem;
      font-weight: 500;
      position: relative;
      text-align: center;

      p {
        font-size: 1.2rem;
        font-weight: 900;
        // font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      }

      &::before {
        content: "o";
        position: absolute;
        top: -8px;
        right: -20px;
        font-size: 2rem;
        font-weight: 500;
      }

      &::after {
        content: "درجة الحرارة";
        position: absolute;
        top: -1.5rem;
        left: -1rem;
        font-size: 1.2rem;
        font-weight: 900;
        width: 7.5rem;
      }
    }
  }
}

.error {
  margin-top: 100px;
  font-size: 2rem;
}